'use client'

import { useEffect, useState } from 'react'

interface DashboardData {
  totalStudents: number
  totalPayments: number
  totalCollected: number
  currentMonthPayments: number
}

interface Student {
  id: number
  fullName: string
  class: string
  payments: Payment[]
}

interface Payment {
  id: number
  month: number
  year: number
  amount: number
  paidAt: string
}

export default function DashboardPage() {
  const [data, setData] = useState<DashboardData | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  useEffect(() => {
    async function fetchData() {
      try {
        const [studentsRes, paymentsRes] = await Promise.all([
          fetch('/api/students'),
          fetch('/api/payments')
        ])

        if (!studentsRes.ok || !paymentsRes.ok) {
          throw new Error('Failed to fetch data')
        }

        const students: Student[] = await studentsRes.json()
        const payments: Payment[] = await paymentsRes.json()

        const currentMonth = new Date().getMonth() + 1
        const currentYear = new Date().getFullYear()

        const currentMonthPayments = payments.filter(
          p => p.month === currentMonth && p.year === currentYear
        ).length

        const totalCollected = payments.reduce((sum, payment) => sum + payment.amount, 0)

        setData({
          totalStudents: students.length,
          totalPayments: payments.length,
          totalCollected,
          currentMonthPayments
        })
      } catch (err: any) {
        setError(err.message || 'Error fetching data')
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [])

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-red-500 text-lg">{error}</div>
      </div>
    )
  }

  if (!data) return null

  const monthNames = [
    'Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni',
    'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'
  ]

  const currentMonth = monthNames[new Date().getMonth()]
  const currentYear = new Date().getFullYear()

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-3xl font-bold text-foreground mb-2">Dashboard</h1>
        <p className="text-muted-foreground">
          Ringkasan data sistem pelaporan SPP
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <div className="bg-card border border-border rounded-lg p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-muted-foreground">Total Siswa</p>
              <p className="text-3xl font-bold text-foreground">{data.totalStudents}</p>
            </div>
            <div className="w-12 h-12 bg-primary/10 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-primary rounded"></div>
            </div>
          </div>
        </div>

        <div className="bg-card border border-border rounded-lg p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-muted-foreground">Total Pembayaran</p>
              <p className="text-3xl font-bold text-foreground">{data.totalPayments}</p>
            </div>
            <div className="w-12 h-12 bg-green-500/10 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-green-500 rounded"></div>
            </div>
          </div>
        </div>

        <div className="bg-card border border-border rounded-lg p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-muted-foreground">Total Terkumpul</p>
              <p className="text-2xl font-bold text-foreground">
                Rp {data.totalCollected.toLocaleString('id-ID')}
              </p>
            </div>
            <div className="w-12 h-12 bg-blue-500/10 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-blue-500 rounded"></div>
            </div>
          </div>
        </div>

        <div className="bg-card border border-border rounded-lg p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-muted-foreground">
                Pembayaran {currentMonth}
              </p>
              <p className="text-3xl font-bold text-foreground">{data.currentMonthPayments}</p>
            </div>
            <div className="w-12 h-12 bg-orange-500/10 rounded-lg flex items-center justify-center">
              <div className="w-6 h-6 bg-orange-500 rounded"></div>
            </div>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <div className="bg-card border border-border rounded-lg p-6">
          <h3 className="text-lg font-semibold mb-4">Informasi SPP</h3>
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Biaya SPP per Bulan</span>
              <span className="font-semibold">Rp 790.000</span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Periode Aktif</span>
              <span className="font-semibold">{currentMonth} {currentYear}</span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Status Sistem</span>
              <span className="px-2 py-1 bg-green-500/10 text-green-500 rounded text-sm font-medium">
                Aktif
              </span>
            </div>
          </div>
        </div>

        <div className="bg-card border border-border rounded-lg p-6">
          <h3 className="text-lg font-semibold mb-4">Statistik Cepat</h3>
          <div className="space-y-4">
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Rata-rata per Siswa</span>
              <span className="font-semibold">
                {data.totalStudents > 0 
                  ? `Rp ${Math.round(data.totalCollected / data.totalStudents).toLocaleString('id-ID')}`
                  : 'Rp 0'
                }
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Tingkat Pembayaran Bulan Ini</span>
              <span className="font-semibold">
                {data.totalStudents > 0 
                  ? `${Math.round((data.currentMonthPayments / data.totalStudents) * 100)}%`
                  : '0%'
                }
              </span>
            </div>
            <div className="flex justify-between items-center">
              <span className="text-muted-foreground">Total Transaksi</span>
              <span className="font-semibold">{data.totalPayments} transaksi</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
